

.carousel {
    display: flex;
    height: 400px;
    width: 100%;
    max-width: 800px;
}

.carousel-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.carousel-card {
    display: flex;
    flex: 1;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 20px;
    -webkit-box-shadow: 0px 0px 11px 3px rgb(93 206 72 / 48%);
    box-shadow: 0px 0px 11px 3px rgb(93 206 72 / 48%);
    opacity: 0;
    pointer-events: none;
    transform: scale(0.8);
    transition: 0.5s ease-in-out;
    /* animation: left .9s ease-in; */
}

.carousel-card.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: visible;
}

