.card-image {
    width: 100%;
    object-fit: cover;
}

.card-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    padding: 40px 30px;
    align-items: flex-end;
}

.card-title {
    color: white;
    font-size: 30px;
}

.carousel-arrow-left,
.carousel-arrow-right {
    position: absolute;
    font-size: 40px;
    top: 50%;
    transform: translate(0, -50%);
    background-color: gainsboro;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    padding-bottom: 7px;
    cursor: pointer;
}

.carousel-arrow-left {
    left: 15px;
}

.carousel-arrow-right {
    right: 15px;
}

